import React, {useState} from 'react';
import {SvgEdit, SvgOk} from "../../../../assets/svg";

import './admin-edit-tables-item.scss';

function AdminEditTablesItem(props) {

    const [readOnly, setReadOnly] = useState(true);

    const [tableNumber, setTableNumber] = useState(props.number);
    const [name, setName] = useState(props.name);
    const [numPersons, setNumPersons] = useState(props.numPersons);
    const [depositPrice, setDepositPrice] = useState(props.depositPrice);

    const icon = readOnly ? <SvgEdit/> : <SvgOk/>;

    function handlerClick() {
        setReadOnly(!readOnly)
    }

    return (
        <div className="admin-edit-tables-item aet-row">
            <div className="aet-row__table-number"><input type="text" value={tableNumber} readOnly={readOnly} onChange={e => setTableNumber(e.target.value)}/></div>
            <div className="aet-row__table-name"><input type="text" value={name} readOnly={readOnly} onChange={e => setName(e.target.value)}/></div>
            <div className="aet-row__guests-count"><input type="text" value={numPersons} readOnly={readOnly} onChange={e => setNumPersons(e.target.value)}/></div>
            <div className="aet-row__deposit-count"><input type="text" value={depositPrice} readOnly={readOnly} onChange={e => setDepositPrice(e.target.value)}/></div>
            <div className="aet-row__photos" >в разработке</div>
            <div className="admin-edit-tables-item__icon aet-row__edit" onClick={handlerClick}>{icon}</div>
        </div>
    )
}

export default AdminEditTablesItem;
